div{
    margin-bottom: 25px;
}


label { 
    color: #adb7bd;
    font-family: 'Lucida Sans', Arial, sans-serif; 
    font-size: 16px; 
    line-height: 26px; 
    text-indent: 30px;
    padding-bottom: 10px; 
    margin: 0; 
}

input{
    padding: 12px 10px;
    margin: 8px 0;
    box-sizing: border-box;
    background-color: rgb(28, 36, 43);
    color: white;
    border: none;
    border-bottom: 2px solid white;
}