.Title { 
  color: #ffffff;
  font-family: 'Lato', sans-serif; 
  font-size: 50px; 
  font-weight: 300; 
  line-height: 58px; 
  margin: 58px 0 50px 0px; 
}

.btnLogin{
  display: inline-block;
  padding: 10px 40px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: whitesmoke;
  background-color: #E86010;
  border: none;
  border-radius: 15px;
  box-shadow: 0 4px #999;
}
  
.btnLogin:hover {
  color: #fff;
  background-color: #663B0C
}
  
.btnLogin:active {
  color: #fff;
  background-color: #663B0C;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.error{
  color: red;
}