.CvTitle{
    color: #ffffff;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 50px;
    font-size: 4vw; 
    letter-spacing: 10px;
    font-weight: 300; 
    line-height: 58px; 
    text-align: left;
    margin: 10px 0 0px 0px; 
}

.CvDiv{
    margin: auto;
    width: 70%;
}

.ImgIcon{
    vertical-align: middle;
    margin-right: 6px;
    border-radius: 4px;
    width: 30px;
}

.ulHList{
    display: flex;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
    justify-content:  space-around;
    font-family:  'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1vw; 
    letter-spacing: 1px;
    align-items: center;
    min-height: 10vh;
    color: white;
    list-style: none;
}

/* unvisited link */
a:link {
    color: #E86010;
}

/* visited link */
a:visited {
    color: #E86010;
}

/* mouse over link */
a:hover {
    color: royalblue;
}

/* selected link */
a:active {
    color: blue;
}

.grid-container {
    box-sizing: border-box;
    display: grid;
    grid-template-columns: auto auto;
    padding: 0px;
}

.grid-item {
    box-sizing: border-box;
    float: left;
    margin-bottom: 0px;
}

.sectionTitle{
    text-align: start;
    text-size-adjust: auto;
    color: white;
    margin-top: 0px;
    margin-bottom: 0px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    letter-spacing: 5px;
    font-size: 30px; 
    font-size: 2vw;
    border-bottom: 6px solid white;
    border-radius: 5px;
    width: 70%;
}


